@charset "UTF-8";

@import "modules/all";
@import "partials/base";
@import "partials/fonts";


@import url(http://fonts.googleapis.com/earlyaccess/droidarabicnaskh.css);

html .mceContentBody {
	direction: rtl;
	unicode-bidi: embed;
	float: right;
	max-width:525px;
}

* {
	font-family: "Droid Arabic Naskh", tahoma, verdana;
	color: inherit;
	font-style: inherit;
	font-weight: inherit;
	font-size:14.4px;
	line-height:25px;
}

h1, h2, h3, h4, h5, h6 {
	font-family: arial, sans-serif;
}

ul, ol {
	margin: 0 2.5em 1.625em 0;
}
blockquote {
	font-style: bold;
}
table {
	text-align: right;
}
strong {
	font-weight: bold;
}
cite, em, i {
	font-style: italic;
}
